<template>
  <div class="base-show-all-row-container f-row-between">
    <div class="title f-i-c">
      <div class="shading" />
      <p>{{ rowTitle }}</p>
    </div>
    <div @click="clickSeeMore()" class="total f-row-between-c">
      <p>全部</p>
      <img src="@/assets/images/row_see_more.png" alt="">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    rowTitle: {
      type: String,
      default: '无标题'
    }
  },
  
  data() {
    return {

    }
  },

  methods: {
    clickSeeMore() {
      this.$emit( 'click' );
    }
  }
}
</script>

<style lang="scss" scoped>
.base-show-all-row-container {
  width: 100%;
  height: 25px;

  .title {
    width: 80%;
    font-size: 18px;
    font-weight: bold;
    color: rgba(51,51,51,1);
    position: relative;

    .shading {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 70px;
      height: 8px;
      background: linear-gradient(90deg,rgba(244,186,65,1) 0%,rgba(255,255,255,1) 100%);
    }
    p {
      position: relative;
    }
  }
  .total {
    width: 45px;

    p {
      font-size: 13px;
      font-weight: bold;
      color: rgba(244,186,65,1);
    }
    img {
      width: 13px;
    }
  }
}
</style>


