<template>
  <div class="morning-parents-banner-container">

    <van-swipe :autoplay="3000" @change="onChange">
      <van-swipe-item
        v-for="(item, index) in bannerData"
        :key="item._id"
      >
        <img
          :src="item.img"
          alt=""
          @click="$emit('clickBanner', item.targetId)"
        >
      </van-swipe-item>

      <div v-if="bannerData && bannerData.length > 0" class="custom-indicator flx" slot="indicator">
        <div v-for="item in bannerData.length" :key="item">
          <div :class="['dot', current + 1 === item ? 'choosed' : '']" />
        </div>
      </div>
    </van-swipe>
    
  </div>
</template>

<script>
import Vue from 'vue';
import { Swipe, SwipeItem } from 'vant';

Vue.use(Swipe).use(SwipeItem);
export default {
  props: {
    bannerData: {
      type: Array,
      default: () => []
    }
  },

  data() {
    return {
      current: 0
    }
  },

  methods: {
    onChange(index) {
      this.current = index;
    }
  }
}
</script>

<style lang="scss" scoped>
.morning-parents-banner-container {
  width: 100%;

  .van-swipe {
    height: 136px;
  }
  img, .van-swipe, .van-swipe-item {
    border-radius: 10px;
  }
  .custom-indicator {
    position: absolute;
    bottom: 10px;
    right: 10px;

    .dot {
      width: 6px;
      height: 6px;
      background: rgba(242,242,242,1);
      border-radius: 50%;
      margin-left: 6px;
    }
    .choosed {
      background-color: #F4BA41;
      width: 15px;
      border-radius:3px;
    }
  }
}
</style>


