import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';

/**
 * todo 根据 banner 类型，获取 banner 列表
 * @param { String } bannerType '0'早安父母  '1'晚安宝贝  '2'成长课堂
 */
export function apiBanner(bannerType) {
  if (!bannerType) {
    throw new Error('bannerType can be 0|1|2 and type is String!');
  }
  
  return request({
    method: 'GET',
    url: `/banner/list-by-type/wx6dd85d5fe5222d19/${bannerType}`,
    auth: true,
    data: {}
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( '接口错误: ', err );
  })
}


