<template>
  <div class="night-parents-container">
    <!-- <img class="banner-back" src="@/assets/images/morning_parents_banner_back.png" alt=""> -->
    <div class="top-block-1" />
    <BaseBanner
      class="night-parents-banner-box"
      :bannerData="bannerData"
      @clickBanner="clickBanner"
    />

    <div class="split-div-1" />

    <!-- // ! 晚安宝贝模块下的所有栏目 -->
    <div v-if="night.categoryList && night.categoryList.length > 0">
      <div
        v-for="(everyCategory, categoryIndex) in night.categoryList"
        :key="everyCategory._id"
        :style="{ 'display': everyCategory.albums.length > 0 ? 'block' : 'none' }"
        :class="[`${getTemplateClassName(everyCategory.template)}`]"
      >
        <BaseShowAllRow
          :rowTitle="everyCategory.name"
          @click="clickCategoryTitle()"
          class="title-row"
        />
        <div class="layout-item-box">
          <div
            v-for="(album, index) in everyCategory.albums"
            :key="album._id"
            class="layout-item"
          >
            <div @click="clickAlbum(album)">
              <component
                :album="album"
                :is="getTemplateNameImport(everyCategory.template)"
              />
            </div>
            <div v-if="index < everyCategory.albums.length - 1" class="split-line-1" />
          </div>
          <div class="split-div-layout" />
        </div>
        
        <div v-show="categoryIndex < night.categoryList.length - 1" class="split-div-2" />
      </div>
    </div>

    <div class="split-div-2" />
    
    <Tabbar class="tabbar-fixed-bottom" />

    <div class="bottom-block-135" />
  </div>
</template>

<script>
import BaseBanner from '@/components/banner/BaseBanner.vue';
import Tabbar from '@/components/tabbar/Tabbar.vue';
import BaseShowAllRow from '@/components/showAllRow/BaseShowAllRow.vue';
import { apiBanner } from '@/service/api/banner';
import { apiCategoryList, ApiAlbumListByClassifyid, apiAlbumInfo } from '@/service/api/program';
import { GOOD_NIGHT_BABY } from '@/utils/category';
import { getTemplateName } from '@/utils/constant';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import Vue from 'vue';
import { Button, Toast } from 'vant';

Vue.use(Button).use(Toast);

export default {
  components: {
    BaseBanner,
    Tabbar,
    BaseShowAllRow
  },

  data() {
    return {
      // 动态组件 样式在('@/assets/style/tabbarPage/homeLayout.scss')
      BaseNewsLayout: (resolve) => require(['@/components/homePageLayout/BaseNewsLayout.vue'], resolve),
      BaseListLayout: (resolve) => require(['@/components/homePageLayout/BaseListLayout.vue'], resolve),
      BaseModularLayout: (resolve) => require(['@/components/homePageLayout/BaseModularLayout.vue'], resolve),

      bannerData: null,

      night: {
        categoryList: null,
        pageNum: 0,
        pageSize: 20
      },

      generalClassify: {
        pageNum: 0,
        pageSize: 20
      }
    }
  },

  created() {
    document.title = '孩子听';
  },

  mounted() {
    this.getBannerData();
    this.getCategoryList();
  },

  methods: {
    // todo 点击 banner
    async clickBanner(albumId) {
      const albumInfoResponse = await apiAlbumInfo(albumId);
      if (!albumInfoResponse || albumInfoResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      const albumInfo = albumInfoResponse.data;
      toAlbumPage(albumInfo.chargeType, albumInfo.isBuy, albumInfo._id);
    },
    
    // todo 获取 banner
    async getBannerData() {
      const bannerResponse = await apiBanner( GOOD_NIGHT_BABY );
      if (!bannerResponse || bannerResponse.type === 'fail') return Toast( '服务器错误, 请联系客服人员' );
      this.bannerData = bannerResponse.data;
    },

    
    // ? 编译不同模板的样式文件
    getTemplateClassName(templateId) {
      return getTemplateName( templateId );
    },

    // ? 编译不同模板
    getTemplateNameImport(templateId) {
      return this[ getTemplateName( templateId ) ];
    },

    async getAlbumLists() {
      const allClassifyAlbums = [];
      for (let i = 0, len = this.night.categoryList.length; i < len; i++) {
        if (this.night.categoryList[i].isDisplay === false) continue;
        allClassifyAlbums.push( this.getAlbumList( this.night.categoryList[i]._id, 0, this.night.categoryList[i].displayAlbumCount ) );
      }

      const allClassifyResults = await Promise.all( allClassifyAlbums );

      // 把各个栏目的 albums 放到 night.categoryList 中
      for (let i = 0, len = this.night.categoryList.length; i < len; i++) {
        if (this.night.categoryList[i].isDisplay === false) continue;
        if (allClassifyResults[i].type === 'fail') continue;
        if (allClassifyResults[i].data.length === 0) {
          this.night.categoryList.splice(i, 1);
          continue;
        }
        this.night.categoryList[i].albums = allClassifyResults[i].data;
      }
    },

    async getAlbumList( classifyId, pageNum = this.generalClassify.pageNum, pageSize = this.generalClassify.pageSize ) {
      return ApiAlbumListByClassifyid( classifyId, pageNum, pageSize);
    },

    async getCategoryList() {
      const categoryResponse = await apiCategoryList( GOOD_NIGHT_BABY, this.night.pageNum, this.night.pageSize );
      if (!categoryResponse || categoryResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.night.categoryList = categoryResponse.data;
      
      this.getAlbumLists();
    },

    seeAllPictureBook() {
      console.log( '绘本故事 - 查看更多' );
      this.$router.push( '/total-album' );
    },

    seeAllVirtue() {
      console.log( '美德故事 - 查看更多' );
      this.$router.push( '/total-album' );
    },

    seeAllBeans() {
      console.log( '小豆故事 - 查看更多' );
      this.$router.push( '/total-album' );
    },

    // todo 跳转至专辑介绍页
    clickAlbum(album) {
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    }
  },
}
</script>

<style lang="scss" scoped>
.night-parents-container {
  width: 100%;
  background: #fff;

  .banner-back {
    position: absolute;
    top: 0;
  }
  .top-block-1 {
    width: 100%;
    height: 30px;
  }
  .night-parents-banner-box {
    box-sizing: border-box;
    padding: 0 16px;
  }

  .split-div-1 {
    width: 100%;
    height: 1px;
    background: rgba(238,238,238,1);
    margin-top: 23px;
  }

  .split-div-2 {
    width: 100%;
    height:10px;
    background:rgba(246,246,246,1);
  }
}
</style>


